/*
  Import Normalize
*/

@import "Normalize/normalize.scss";

/*
  Variables
*/

@import "Variables/variables.scss";

/*
  Helpers
 */
@import "Helpers/helpers.scss";
@import "Custom-Icon-Font/icon-font.scss";
@import "Helpers/grid.scss";


/*
  Base Styles
*/

*, *:before, *:after {
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

img {
    max-width: 100%;
    height: auto;
    display: block;
}

a {
  color: $rw-black;
  transition: color 0.3s;
  &:hover, &:active, &:focus {
    color: white;
  }
}


p {
  line-height: 1.25;
  @extend %font;
}

.wrap {
  background: $highlight;
  min-height: 100vh;
  padding-bottom: 80px;
  position: relative;
}

%font {
    font: {
      family: "ff-dagny-web-pro", sans-serif;
    }
}

%font-heading  {
   font-family: "franklin-gothic-urw", Tahoma, Verdana, sans-serif;
   font-size: 1em;
   font-weight: 600;
   @media (min-width: $break) { font-weight: 900;}
   color: $heading-color;
  }

%display {
        @extend %font-heading;
        background: $rw-black;
      @media (min-width: $break) {
        font-size: 3.5em;
        display: block;
      }
    }

%content-wrap {
    @media (min-width: $break) {
      @include clearfix;
      max-width: $wrap;
      margin: $padding auto;
      padding: $padding;
    }
}

/*
Nav
*/

nav { background: $rw-black;}

.nav {
    @include clearfix;
    @extend %display;
    max-width: $wrap;
    margin: 0 auto;
    padding: 0;
    text-align: center;
  @media (min-width: $break) {
    text-align: left;
    }

 & > li {
      position: relative;
      list-style: none;
      display: inline-block;
      transition: background 0.3s ease-in;
      &:hover, &:focus {background: $highlight;}
    }

  a {
    text-decoration: none;
    display: block;
    padding: $padding/2;
    color: $heading-color;
    @media (min-width: $break){
      padding-left: $padding;
    }
  }
}




/*
Intro
 */

 .intro {
  @extend %content-wrap;
  @include clearfix;
  margin: 0 auto;
  background: $highlight;
  @media (min-width: $break) {
    padding:3*$padding $padding;
  //  max-width: $wrap;
  }
  figure {
      @include clearfix;
  }
  img {
      @media (min-width: $break) {
        display: block;
        float: right;
        max-width: 50%;
        border-radius: 0.25em;
      }
    }
  figcaption {
      padding: 0 $padding;
      position: relative;
      @media (min-width: $break) {
        display: block;
        float: left;
        max-width: 50%;
      }
    }
 }
.caption {
      @extend %font-heading;
      background: none;
      position: absolute;
      top: -6*$padding;
    h2 {font-weight: 900;}
    h2, p {
      margin: 0;
      padding: 0;
        }
  @media (min-width: $break) {
        position: relative;
        top: 0;
    h2 {font-size: 3.5em;}
    p { font-size: 1.5em;}
    }
}

/*
Portfolio
*/

.web-design, .photo-editing,  {
    @extend %content-wrap;
    @extend %group;
    a {display: block;}
}

.photo-editing article, .work article {
    @include grid(4);
    a {text-decoration: none;}
}

.web-design article {
  @include grid(6);
  a {text-decoration: none;}
}

.example-wrap {

  &:hover, &:focus, &:active{
    img{
    box-shadow: 0.1em 0.1em 0.2em rgba($rw-black, 0.3);
    transform: scale(1.05);
    }
    figcaption {
    text-shadow: 0.1em 0.1em 0.2em rgba($rw-black, 0.3);
    }
  }

  img {
    max-height: 20rem;
    width: auto;
    margin: 0 auto;
    border-radius: 0.25em;
    transition: box-shadow 0.7s, transform 0.7s;
  }
  & > figcaption {
      text-align: center;
      padding: 0 $padding;
      transition: text-shadow 0.7s;
    p {
      @extend %font-heading;
       font-weight: 200;
      }
    }
  }

/*
Portfolio Subpages
 */
.portfolio {
  @extend %content-wrap;
  @media (min-width: $break){
    margin: $padding*4 auto;
    position: relative;

  }
}

.img-wrap {
  width: 100%;
  margin: 0;
  img {
     margin-left: auto;
     margin-right: auto;  
    }
 @media (min-width: $break) {
      max-width: 50%;
      float: left;
      padding-right: $padding;
    img {
      //max-height: 90vh;
      border-radius: 0.25em;
       }
    }
  }

.content-wrap {
      @media (min-width: $break) {
        //margin: 5% 0 0;
        padding-left: 2.5%;
        max-width: 50%;
        float: left;
}

    .title {
        @extend %display;
        text-align: center;
        margin: 0 0 $padding;
      @media (min-width: $break) {
        color: $rw-black;
        display: block;
        text-align: center;
        background: transparent;
      }

    }
    p {
      padding: 0 $padding;
    }
}


/*
Example Projects
 */

.section-title {
  background: $rw-black;
  h2 {
    @extend %display;
    margin: 0 auto;
    padding: $padding;
    position: relative;
    max-width: $wrap;
    transition: transform 0.7s;
  }
}

.work {
    max-width: $wrap;
    margin: 0 auto;
    @extend %group;
    img {
        max-height: 15em;
    }
  }

/*
About Page
*/

.about, .projects {
  @extend %content-wrap;
  @extend %group;
}

.me, .blog {
  @include grid(6);

  h2, h3 {
    @extend %font-heading;
    color: $rw-black;
    font-size: 1.5em;
    margin: 0;
  }
}

.blog {
  text-align: center;
  p {
    @extend %font-heading;
    color: $rw-black;
    font-weight: 200;
    font-size: 1em;
    margin-bottom: 0;
    transition: all 0.7s;
  }
  a {
    text-decoration: none;
  }
  li {
      &:hover, &:active, &:focus {
      p, h3 {
        color: white;
        transform: scale(1.1);
        text-shadow: 0.1em 0.1em 0.2em rgba($rw-black, 0.3);
        }
      }
  }

  h2, h3 {
    display: inline-block;
    padding-bottom: 0.5em;
    border-bottom: 0.25em solid $rw-black;
  }
  h3 {
    font-size: 1.25em;
    transition: all 0.7s;
  }

  ul {
    list-style: none;
    padding: 0;
    text-align: center;
  }
}



/*
Blog Posts
*/
.post {
  @extend %content-wrap;
  margin: 0 auto;
  max-width: $wrap/2;
  padding: $padding;
  h2 {
    @extend %font-heading;
    color: $rw-black;
    line-height: 0;
    font-size: 1.5em;
    margin-bottom: 0;
  }
  h2 + p {
    @extend %font-heading;
    color: $rw-black;
    font-weight: 200;
    font-size: 1em;
  }
}

pre {
  margin: $padding*2 0;
  padding: $padding;
  background: lighten($highlight, 25%);
  border-left: 0.25em solid $rw-black;
  line-height: 1;
  transition: all 0.7s;
  code {
    font-size: 0.8em;
    font-weight: 400;
  }
  &:hover {
    background: darken($highlight, 25%);
    border-color: white;
    color: white;
  }
}

/*
Footer
 */

.main-footer {
  width: 100%;
  background: $rw-black;
  font-family: 'Custom-Icon-Font';
  position: absolute;
  top: calc(100% - 48px);
  @media (min-width: $break){
    top: calc(100% - 80px);
  }
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    text-align: center;
    li {
      display: inline-block;
      padding: $padding;
    }
    a {
      text-decoration: none;
    }
  }
}
    .icon {
      font-style: normal;
      display: block;
      color: white;
      opacity: 0.4;
      font-size: 1em;
      height: auto;
      width: auto;
      transition: transform 0.5s ease-out, opacity 0.3s ease-in;
      &:hover, &:focus {
        opacity: 1;
        transform: scale(1.5);
        color: $highlight;
      }
      @media (min-width: $break) {
        font-size: 3em;
      }
    }

